.burger {
  width: 22px;
  height: 12px;
  background-color: #191919;
  padding: 0;
  position: relative;
}

.burger.active {
  width: 18px;
  height: 18px;
}

.burger .burger__stick {
  height: 2px;
  width: 22px;
  background-color: rgba(255, 255, 255, .8);
  transition: transform .4s;
  display: block;
}

.burger.active .burger__stick {
  transform: scale(0);
}

.burger:before, .burger:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: rgba(255, 255, 255, .8);
  transition: transform .4s;
  display: block;
  position: absolute;
}

.burger:before {
  top: 0;
}

.burger:after {
  bottom: 0;
}

.burger.active:before {
  border-radius: 10px;
  top: 50%;
  transform: rotate(45deg)translateY(-50%);
}

.burger.active:after {
  border-radius: 10px;
  bottom: 50%;
  transform: rotate(-45deg)translateY(50%);
}

